<template>
    <div class="vmc-uploader-wrapper vmc-relative">
        <slot></slot>
        <input 
            type="file"
            class="vmc-uploader-input vmc-absolute"
            v-bind="$attrs"
            ref="input"
            @change="onValueChange"/>
    </div>
</template>

<script>
    export default {
        name: 'vmc-uploader',
        props: {
            disabled: Boolean,
            beforeRead: Function,
            afterRead: Function,
            resultType: {
                type: String,
                default: 'dataUrl'
            }
        }, 
        data() {
            return {

            }
        },
        methods: {
            onValueChange(e) {
                if (this.disabled) return
                const file = e.target.files[0]
                if (!file || (this.beforeRead && !this.beforeRead(file))) return
                if (!/\.(jpe?g|png|gif)$/i.test(file.name)) return
                const reader = new FileReader()
                
                reader.onload = e => {
                    this.afterRead && this.afterRead({
                        file,
                        content: e.target.result
                    });
                    this.$refs.input && (this.$refs.input.value = '');
                }

                this.resultType === 'dataUrl' 
                    ? reader.readAsDataURL(file)
                    : this.resultType === 'text'
                    ? reader.readAsText(file)
                    : null
                /*
                    readAsDataURL方法会读取指定的 Blob 或 File 对象。读取操作完成的时候，readyState 会变成已完成（DONE），并触发 loadend 事件，同时 result 属性将包含一个data:URL格式的字符串（base64编码）以表示所读取文件的内容
                    readAsText 方法可以将 Blob 或者 File 对象转根据特殊的编码格式转化为内容(字符串形式)
                */
            }
        }
    }
</script>

<style lang="less" scoped>
    .vmc-uploader-wrapper{
        .vmc-uploader-input{
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            z-index: 200;
        }
    }
</style>


