<template>
	<div class="fixedfilter-ladder-item-wrapper">
		<Scroll
			:preventDefault="true"
			:click="true"
			:data="rlist"
			class="list-view"
			:style="listViewStyles">
			<div class="fixedfilter-ladder-item-inner">
				<div class="type-wrapper vmc-flex vmc-flex-center vmc-flex-raround" v-if="Array.isArray(rlist) && rlist.length > 1">
					<span class="vmc-inlineb" v-for="(o, i) in rlist" :key="i">{{o.name}}</span>
				</div>
				<div class="content-wrapper vmc-flex vmc-flex-rstart vmc-flex-cstart">
					<div class="pstep-wrapper">
						<list-view
							:height="200"
							:list="rlist">
							<ul>
								<li class="step-item" v-for="(o, i) in rlist[0].steplist" :key="i"
									:class="{'current': currentStepIndex === i}"
									@click.stop="switchStep(i)">
									{{o.label}}
								</li>
							</ul>
						</list-view>
					</div>
					<div class="sstep-wrapper">
						<list-view
							:height="200"
							:list="substeplist">
							<ul>
								<li class="step-item sstep-item vmc-1px-b" v-for="(o, i) in substeplist" :key="o.value"
									:class="[{'vmc-1px-b-3385ff current': currentStepIndex === saveMap.stepCurrentIndex && i === saveMap.substepCurrentIndex}]"
									@click="select(o, i)">
									{{o.label}}
								</li>
							</ul>
						</list-view>
					</div>
				</div>
			</div>
		</Scroll>
	</div>
</template>

<script>
	import Scroll from 'packages/base/scroll'
	import ListView from 'packages/base/listview'
	export default {
		props: {
			list: {
				type: Object,
				default: []
			},
			maxHeight:{
				type: Number,
				default: 400
			}
		},
		components: {
			Scroll,
			ListView
		},
		data() {
			return {
				rlist: [
					{
						steplist:[
							{
								substeplist: []
							}
						]
					}
				],
				currentModuleIndex: 0,
				currentStepIndex: 0,
				substepCurrentIndex: -1,
				currentSubStepSelected: false,
				saveMap: {
					stepCurrentIndex: -1,
					substepCurrentIndex: -1
				}
			}
		},
		created() {
		},
		mounted() {
			this.rlist = [this.list]
		},
		computed: {
			listViewStyles() {
				return {
					maxHeight: `${this.maxHeight}px`
				}
			},
			substeplist() {
				return this.rlist[0].steplist[this.currentStepIndex].substeplist
			}
		},
		methods: {
			switchStep(i) {
				this.currentStepIndex = i
			},
			select(o, i){
				this.substepCurrentIndex = i
				this.saveMap.stepCurrentIndex = this.currentStepIndex
				this.saveMap.substepCurrentIndex = i
				this.$emit('on-select', this.currentStepIndex, i, o)
			}
		},
		watch: {
			"saveMap.stepCurrentIndex"() {
			},
			"saveMap.substepCurrentIndex"() {
			}
		}
	}
</script>


<style lang="less" scoped>
	.fixedfilter-ladder-item-wrapper{
		background-color: #fff;
	}
	.list-view{
		overflow: hidden;
	}
	.fixedfilter-ladder-item-inner{
		height: 100%;
		background-color: #fff;
		.type-wrapper{
			padding: 4px 0;
			color: #666;
			font-size: 13px;
		}
		.pstep-wrapper{
			flex: 0 0 30%;
		}
		.sstep-wrapper{
			flex: 1;
		}
		.step-item{
			padding: 8px 0 4px 10px;
			background: #eee;
			color: #666;
			&.current{
				background-color: #fff;
			}
		}
		.sstep-item{
			color: #666;
			font-size: 13px;
			margin-left: 10px;
		}
		.sstep-wrapper{
			.step-item{
				padding: 10px 0 6px 10px;
				background-color: #fff;
				&.current{
					color: #3385ff;
				}
			}
		}
	}
</style>


