<template>
    <transition :name="transition ? 'vmc-slide-bottom' : ''">
        <div
            v-clickoutside:touchstart="{callback: onOutsizeClick, filter: {tagName: 'input', types: ['text', 'password', 'textarea']}}"
            class="vmc-numberkeyboard-wrapper vmc-flex vmc-flex-center vmc-flex-wrap vmc-1px-t"
            v-show="showKeyboard"
            @touchstart.stop.prevent="focus"
            @touchmove="blurKey"
            @touchend="blurKey"
            @touchcancel="blurKey"
            @transitionend="onAnimationend">
            <span
              v-for="(o, k) in keys"
              :key="k"
              :data-key="k"
              class="vmc-inlineb vmc-numberkeyboard-item vmc-flex vmc-flex-center vmc-1px-rb"
              :class="{
                  'vmc-numberkeyboard-del': k === 11,
                  'vmc-numberkeyboard-grey': k === 9 || k === 11,
                  'vmc-numberkeyboard-active': k === active
              }"
              v-text="o"></span>
        </div>
    </transition>
</template>


<script>
    import Clickoutside from '../directives/clickoutsize'
    export default {
        name: 'vmc-numberkeyboard',
        props: {
            value: {
              type: [Boolean, Number],
              default: false
            },
            transition: {
                type: Boolean,
                default: true
            },
            extraKey: {
                type: String,
                default: ''
            }
        },
        data() {
            return {
                active: -1,
                showKeyboard: false
            }
        },
        directives: {
            Clickoutside
        },
        methods: {
          focus(e) {
            this.active = parseInt(event.target.dataset.key)
            if (this.active === 11) {
                this.$emit('delete');
            } else if (!isNaN(this.active)) {
                const key = this.keys[this.active];
                if (key !== '') {
                    this.$emit('select', key);
                }
            }
          },
          blurKey() {
              this.active = -1
          },
          onAnimationend(){
              console.log('完成动画')
          },
          onOutsizeClick() {
            this.showKeyboard = false
            this.$emit('input', false)
          }
        },
        computed: {
            keys() {
                const results = []
                for (let i = 0; i < 12; i++) {
                    results.push(
                        i === 10 ? 0 : i < 9 ? i + 1 : i === 9 ? this.extraKey : ''
                    )
                }
                return results
            }
        },
        watch: {
          value() {
            this.showKeyboard = this.value
          }
        }
    }
</script>

<style lang="less" scoped>
    .vmc-numberkeyboard-wrapper{
        z-index: 2002;
        position: fixed;
        bottom: 0;left: 0;right: 0;
        background: white;
        // 用户将无法在该文本块中开始选择文本
        user-select: none;
        .vmc-numberkeyboard-item{
           width: calc( 100% / 3 );
           height: 54px;
           font-size: 24px;
           color: #333;
        }
        .vmc-numberkeyboard-del{
          background: url(data:image/png;base64,) no-repeat center center;
          background-size: auto 15px;
        }
        .vmc-numberkeyboard-grey{
          background-color: #F3F3F6;
        }
        .vmc-numberkeyboard-active{
          background-color: #F3F3F6;
        }
    }
</style>



