<template>
	<div class="vmc-hotcity-wrapper">
		<div class="vmc-hotcity-inner">
			<scroll 
			  @scroll="scroll"
	          :listen-scroll="true"
	          :probe-type="3"
	          :data="citys"
	          class="vmc-list-view"
	          ref="listview">
	          <ul class="">
	          	<li class="vmc-list-group" v-for="(group, index) in citys" :key="index" ref="listGroup">
	          		<h2 class="list-group-title">{{group.name}}</h2>
	          		<ul>
	          			<li class="list-group-item" 
	          				:class="{'vmc-1px-b': cityindex !== group.cities.length - 1}" 
	          				v-for="(city, cityindex) in group.cities" 
	          				:key="city.cityid"
	          				v-touchaction="{stopPropagation: false }"
	          				@click="selectItem(city)"
	          				>
	          				{{city.name}}
	          			</li>
	          		</ul>
	          	</li>
	          </ul>
	          <div class="vmc-shortcut">
	          	<ul>
	          		<li class="vmc-shortcut-item" 
	          			v-for="(group, index) in shortcutlist" :key="index"
	          			:data-index="index"
	          			:class="{'current':currentIndex===index}"
	          			@touchstart.stop.prevent="onShortcutTouchStart" 
	          			@touchmove.stop.prevent="onShortcutTouchMove"
           				@touchend.stop>
	          			{{index === 0 ? group.name.split('热门城市')[0] : group.name}}
	          		</li>
	          	</ul>
	          </div>
	          <div class="vmc-hotcity-fixed-title" v-show="fixedtitle" ref="fixedTitle">
	          	<div class="title">{{fixedtitle}}</div>
	          </div>
	        </scroll>
		</div>
	</div>
</template>

<script>
	import Scroll from 'packages/base/scroll'
	import citys from './citys'
	import Touchaction from 'packages/directives/touchaction'
	export default {
		data() {
			return {
				citys,
				shortcutlist: citys.slice(1),
				currentIndex: 0,
				scrollY: -1,
				diff: -1,
				show: false
			}
		},
		directives: {
			Touchaction
		},
		components: {
			Scroll
		},
		created() {
			this.listHeight = []
			this.touch = {}
		},
		mounted() {
			this.$nextTick(() => {
				this.refreshScroll()
				this._calculateHeight()
			})
		},
		computed: {
			fixedtitle() {
				if (this.scrollY > 0) {
		          return ''
		        }
		        return this.citys[this.currentIndex] ? this.citys[this.currentIndex].name : ''
			}
		},
		methods: {
			scroll(pos) {
				this.scrollY = pos.y
			},
			refreshScroll() {
				this.$refs.listview && this.$refs.listview.refresh()
			},
			_calculateHeight() {
				if (!this.$refs.listGroup) return
				this.listHeight = []
		        const list = this.$refs.listGroup
		        let height = 0
		        this.listHeight.push(height)
		        Array.from(list).forEach(item => {
		          height += item.clientHeight
		          this.listHeight.push(height)
		        })
			},
			selectItem(city) {
				this.$emit('on-selectedcity', city)
				this.show = false
				this.$emit('input', false)
			},
			_scrollTo(index) {
		        if (!index && index !== 0) {
		          return
		        }
		        if (index < 0) {
		          index = 0
		        } else if (index > this.listHeight.length - 2) {
		          index = this.listHeight.length - 2
		        }
		        this.$refs.listview.scrollToElement(this.$refs.listGroup[index], 0)
		        this.scrollY = this.$refs.listview.scroll.y
		    },
			onShortcutTouchStart(e) {
				let anchorIndex = e.target.getAttribute('data-index')
				let firstTouch = e.touches[0]
		        this.touch.y1 = firstTouch.pageY
		        this.touch.anchorIndex = anchorIndex
		        this._scrollTo(anchorIndex)
			},
			onShortcutTouchMove(e) {
				let firstTouch = e.touches[0]
		        this.touch.y2 = firstTouch.pageY
		        let delta = (this.touch.y2 - this.touch.y1) / 18 | 0
		        let anchorIndex = parseInt(this.touch.anchorIndex) + delta
		        this._scrollTo(anchorIndex)
			}
		},
		watch: {
			scrollY(newY) {
				const listHeight = this.listHeight
		        // 当滚动到顶部，newY>0
		        if (newY > 0) {
		          this.currentIndex = 0
		          return
		        }
		        // 在中间部分滚动
		        for (let i = 0; i < listHeight.length - 1; i++) {
		          let height1 = listHeight[i]
		          let height2 = listHeight[i + 1]
		          if (-newY >= height1 && -newY < height2) {
		            this.currentIndex = i
		            this.diff = height2 + newY
		            return
		          }
		        }
		        // 当滚动到底部，且-newY大于最后一个元素的上限
		        this.currentIndex = listHeight.length - 2
			},
			diff(newVal) {
		        let fixedTop = (newVal > 0 && newVal < 40) ? newVal - 40 : 0
		        if (this.fixedTop === fixedTop) {
		          return
		        }
		        this.fixedTop = fixedTop
		        this.$refs.fixedTitle.style.transform = `translate3d(0,${fixedTop}px,0)`
		    }
		}
	}
</script>

<style scoped lang="less">
	.vmc-hotcity-wrapper{
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 10000;
		transition: .2s ease-in-out;
		-webkit-transition: .2s ease-in-out;
		.list-group-title{
			padding: 16px 16px 10px;
		    line-height: 1;
		    font-size: 14px;
		    color: #999;
		    background: #f7f7f7;
		}
		.vmc-hotcity-inner{
			width: 100%;
		    height: 100%;
		    margin: 0 auto;
		    border-radius: 2px;
		    background: #fff;
		    box-shadow: 0 1px 3px rgba(0,0,0,.1);
		    .vmc-list-view{
		    	position: relative;
			    width: 100%;
			    height: 100%;
			    overflow: hidden;
		    }
		    .vmc-shortcut{
		    	position: absolute;
			    z-index: 30;
			    right: 0;
			    font-family: Helvetica;
			    top: 50%;
			    -webkit-transform: translateY(-50%);
			    transform: translateY(-50%);
			    .vmc-shortcut-item{
			    	padding: 6px 16px 0;
				    line-height: 1;
				    text-align: center;
				    font-size: 12px;
				    color: #999;
				    &.current{
				    	color: #333;
				    }
			    }
		    }
		    .vmc-hotcity-fixed-title{
	    	    position: absolute;
			    top: 0;
			    left: 0;
			    width: 100%;
		    	.title{
					line-height: 1;
				    padding: 16px 16px 10px;
				    font-size: 14px;
				    color: #999;
				    background: #f7f7f7;
		    	}
		    }
		}
		.list-group-item{
			position: relative;
		    height: 50px;
		    line-height: 50px;
		    padding: 0 16px;
		    font-size: 14px;
		    color: #333;
		}
	}
</style>


