<template>
	<div class="fixedfilter-multiselect-item-wrapper">
		<Scroll
			:preventDefault="true"
			:click="true"
			:data="rlist"
			class="list-view"
			:style="listViewStyles">
			<div class="fixedfilter-multiselect-item-inner">
				<ul>
					<li class="item-link"
						v-for="(o, i) in rlist" :key="o.value">
						<h4 class="type-title">{{o.typename}}</h4>
						<ul class="type-item-list vmc-flex vmc-flex-center vmc-flex-wrap vmc-flex-rstart"
							:class="{'vmc-1px-b': i !== rlist.length - 1}">
							<li class="type-item-link vmc-1px-radius" v-for="(typeitem, k) in o.list" :key="typeitem.value" @click.stop="tickChoice(typeitem)" 
							:class="{'selected vmc-1px-radius-3385ff': !!typeitem.selected}">
								{{typeitem.label}}
							</li>
						</ul>
					</li>
				</ul>
			</div>
		</Scroll>
		<div class="opear-btns vmc-flex vmc-flex-center">
			<button class="vmc-btn vmc-1px-radius" type="button" @click.stop="reset" v-touchaction="{stopPropagation: true }">重置</button>
			<button class="vmc-btn vmc-btn-primary" type="button" @click="sure">确定</button>
		</div>
	</div>
</template>

<script>
	import Scroll from 'packages/base/scroll'
	import utils from 'packages/utils'
	import Touchaction from 'packages/directives/touchaction'
	export default {
		props: {
			list: {
				type: Array,
				default: []
			},
			maxHeight:{
				type: Number,
				default: 300
			}
		},
		directives: {
			Touchaction
		},
		data() {
			return {
				rlist: []
			}
		},
		created() {
			let cloneList = utils.clone(this.list)
			cloneList.forEach(item => {
				item.list.forEach(o => {
					o.selected = false
				})
			})
			this.rlist = cloneList
		},
		mounted() {
		},
		methods: {
			tickChoice(item) {
				item.selected = !item.selected
			},
			reset() {
				this.rlist.forEach(item => {
					item.list.forEach(o => {
						o.selected = false
					})
				})
			},
			sure() {
				const cloneRlist = utils.clone(this.rlist)
				let res = []
				res = cloneRlist.filter(item => {
					item.list = utils.clone(item.list.filter(o => o.selected))
					return item.list.length
				})
				this.$emit('on-sure', res)
			}
		},
		computed: {
			listViewStyles() {
				return {
					maxHeight: `${this.maxHeight}px`
				}
			}
		},
		components: {
			Scroll
		}
	}
</script>

<style lang="less" scoped>
	.fixedfilter-multiselect-item-wrapper{
		background-color: #fff;
	}
	.list-view{
		overflow: hidden;
	}
	.fixedfilter-multiselect-item-inner{
		height: 100%;
		background-color: #fff;
		padding: 0 10px;
		.item-link{
			padding: 10px 0;
			.type-title{
				font-size: 12px;
				padding: 0 0 10px 0;
			}
			.type-item-list{
				.type-item-link{
					margin-bottom: 16px;
					flex: 0 0 78px;
					text-align: center;
					color: #666;
					font-size: 12px;
					padding: 6px 0;
					margin-right: 10px;
					&.selected{
						color: #3385ff;
						background: rgba(51,133,255, .1);
					}
				}
			}
			&.current{
				color: #3385ff;
			}
		}
	}
	.opear-btns{
		position: absolute;
		bottom: -50px;
		left:0;
		right:0;
		padding: 0 10px;
		height: 50px;
		background-color: #eee;
		.vmc-btn{
			flex:1;
			margin-right: 10px;
			&:last-child{
				margin-right: 0;
			}
		}
	}
</style>


