<template>
	<Scroll class="list-view"
		:data="rlist"
		:style="listViewStyles">
		<div class="list-view-inner">
			<slot></slot>
		</div>
	</Scroll>
</template>


<script>
	import Scroll from 'packages/base/scroll'
	export default {
		props: {
			list: {
				type: Array,
				default: () => []
			},
			height: {
				type: Number,
				default: 300
			}
		},
		data() {
			return {
				rlist: []
			}
		},
		computed: {
			listViewStyles() {
				return {
					maxHeight: this.height + 'px'
				}
			}
		},
		mounted() {
			this.rlist = this.list
		},
		components: {
			Scroll
		}
	}
</script>

<style scoped lang="less">
	.list-view{
		overflow: hidden;
	}
</style>


