<template>
	<section class="vmc-fixedfilter-wrapper" :style="fixedfilterWrapperStyles">
		<div class="vmc-fixedfilter-menu vmc-1px-b vmc-flex vmc-flex-center vmc-flex-raround">
			<div v-for="(item, index) in lists" :class="{'current': !!item.currentSelectItem && item.currentSelectItem.length}" :key="index" @click="toggleFilter(item, index)">
				<span>{{item.modelname}}</span>
				<i :class="`ion-ios-arrow-${item.opened ? 'up' : 'down'}`"></i>
			</div> 
		</div>
		<div class="select-wrapper" :style="selectWrapperStyles">
			<div class="group-wrapper" v-for="(item, index) in lists" :key="index" @click.stop :style="{'z-index': currentSelectIndex === index ? 210 : 200}">
				<transition name="vmc-slide-top">
					<fixedfilter-select-item
						:list="item.list"
						@on-select="onSelectOne"
						v-if="item.type==='select'" v-show="item.opened">
					</fixedfilter-select-item>
					<fixedfilter-multiselect-item
						:list="item.list"
						@on-sure="onSure"
						v-if="item.type==='multiselect'" v-show="item.opened">
					</fixedfilter-multiselect-item>
					<fixedfilter-ladder-item
						:list="item.list"
						@on-select="onSelectLadderOne"
						v-if="item.type==='ladder'" v-show="item.opened">
					</fixedfilter-ladder-item>
				</transition>
			</div>
			<transition name="fade" :duration="400" @touchmove.stop.prevent @touchstart.stop.prevent>
				<div class="mask" v-show="showmask" @click="closeSelect"></div>
			</transition>
		</div>
	</section>
</template>

<script>
	import utils from 'packages/utils'
	import FixedfilterSelectItem from './fixedfilter-select-item'
	import FixedfilterMultiselectItem from './fixedfilter-multiselect-item'
	import FixedfilterLadderItem from './fixedfilter-ladder-item'
	export default {
		props: {
			filterlists: {
				type: Array,
				default: []
			},
			distanceTop: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				lists: [],
				showmask: false,
				currentSelectIndex: 0
			}
		},
		computed: {
			fixedfilterWrapperStyles() {
				return {
					top: this.distanceTop + 'px'
				}
			},
			selectWrapperStyles() {
				return {

				}
			}
		},
		mounted() {
			this.lists = utils.clone(this.filterlists)
		},
		methods: {
			closeSelect() {
				this.lists[this.currentSelectIndex].opened = false
				this.showmask = false
			},
			toggleFilter(item, index){

				item.opened = !item.opened
				if (item.opened) {
					this.showmask = true
					this.currentSelectIndex = index
				} else {
					this.showmask = false
				}

				this.lists.forEach((o,i) => {
					if (i !== index && o.opened) {
						o.opened = false
					}
				})
			},
			onSure(item) {
				this.lists[this.currentSelectIndex].opened = false
				this.lists[this.currentSelectIndex].currentSelectItem = utils.clone(item)
				this.showmask = false
				this.$emit('on-change', utils.clone(item), this.currentSelectIndex)
			},
			onSelectOne(item, index) {
				this.lists[this.currentSelectIndex].modelname = item.label
				this.lists[this.currentSelectIndex].currentSelectItem = [utils.clone(item)]
				this.lists[this.currentSelectIndex].opened = false
				this.showmask = false
				this.$emit('on-change', utils.clone(item), this.currentSelectIndex)
			},
			onSelectLadderOne(stepindex, substepindex, row) {
				this.lists[this.currentSelectIndex].modelname = row.label
				this.lists[this.currentSelectIndex].currentSelectItem = [utils.clone(row)]
				this.lists[this.currentSelectIndex].opened = false
				this.showmask = false
				this.$emit('on-change',{
					stepindex, 
					substepindex, 
					row: utils.clone(row)
				}, this.currentSelectIndex)
			}
		},
		components: {
			FixedfilterSelectItem,
			FixedfilterMultiselectItem,
			FixedfilterLadderItem
		},
		watch: {
			
		}
	}
</script>

<style lang="less" scoped>
	.vmc-fixedfilter-wrapper{
		position: fixed;
		left:0;
		right: 0;
		height:40px;
		background-color: #fff;
		.vmc-fixedfilter-menu{
			height: 100%;
			.current{
				color: #3385ff;
			}
		}
		.select-wrapper{
			position: fixed;
			top: 40px;
			left:0;
			right:0;
			bottom: 0;
			.group-wrapper{
				position: absolute;
				left: 0;
				top: 0;
				right: 0;
				z-index: 200;
			}
			.mask{
				background-color: rgba(0, 0, 0, .2);
				position: absolute;
				left:0;
				top: 0;
				bottom: 0;
				right: 0;
			}
		}
	}
</style>


