<template>
	<div class="fixedfilter-select-item-wrapper">
		<Scroll
			:preventDefault="true"
			:data="rlist"
			class="list-view"
			:click="true"
			:style="listViewStyles">
			<div class="fixedfilter-select-item-inner">
				<ul>
					<li class="item-link"
						:class="[{'vmc-1px-b': i !== rlist.length - 1}, {'vmc-1px-b-3385ff': (i === selectIndex && i !== rlist.length - 1)}, {'current': i === selectIndex}]"
						v-for="(o, i) in rlist" :key="o.value"
						@click.stop="selectOne(o, i)">
						{{o.label}}
					</li>
				</ul>
			</div>
		</Scroll>
	</div>
</template>

<script>
	import Scroll from 'packages/base/scroll'
	export default {
		props: {
			list: {
				type: Array,
				default: []
			},
			maxHeight:{
				type: Number,
				default: 350
			}
		},
		components: {
			Scroll
		},
		data() {
			return {
				rlist: [],
				selectIndex: -1
			}
		},
		mounted() {
			this.rlist = this.list
		},
		computed: {
			listViewStyles() {
				return {
					maxHeight: `${this.maxHeight}px`
				}
			}
		},
		methods: {
			selectOne(o, index) {
				this.selectIndex = index
				this.$emit('on-select', o, index)
			}
		}
	}
</script>


<style lang="less" scoped>
	.fixedfilter-select-item-wrapper{
		background-color: #fff;
	}
	.list-view{
		overflow: hidden;
	}
	.fixedfilter-select-item-inner{
		height: 100%;
		background-color: #fff;
		padding: 0 10px;
		.item-link{
			color:#666;
			padding: 10px 0;
			&.current{
				color: #3385ff;
			}
		}
	}
</style>


