
import Actionsheet from './actionsheet';
import Dialog from './dialog/index.js';
import Toast from './toast/index.js';
import Picker from './picker';
import PullRefresh from './pull-refresh'
import CellSwipe from './cell-swipe'
import NumberKeyboard from './numberkeyboard'
import Uploader from './uploader'
import Hotcity from './hotcity'
import Fixedfilter from './fixedfilter'

const VMC_KEY = 'vmc'
const vmc = {
	[VMC_KEY + 'Actionsheet']: Actionsheet,
	[VMC_KEY + 'Dialog']: Dialog,
	[VMC_KEY + 'Picker']: Picker,
	[VMC_KEY + 'PullRefresh']: PullRefresh,
    [VMC_KEY + 'CellSwipe']: CellSwipe,
    [VMC_KEY + 'NumberKeyboard']: NumberKeyboard,
    [VMC_KEY + 'Uploader']: Uploader,
    [VMC_KEY + 'Hotcity']: Hotcity,
    [VMC_KEY + 'Fixedfilter']: Fixedfilter
}

const install = function (Vue, opts = {}) {
    Object.keys(vmc).forEach((key) => {
        Vue.component(key, vmc[key]);
        Vue.prototype.$Dialog = Dialog;
        Vue.prototype.$Toast = Toast;
    });
};

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}
export default Object.assign(vmc, {install})



